.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 280px;
  background: #1f2937; /*  */
  color: white;
  transition: all 0.3s ease;
  z-index: 1000;
  overflow-y: auto;
  box-shadow: 4px 0 20px rgba(0, 0, 0, 0.1);
}

.sidebar.collapsed {
  width: 80px;
}

/*  */
.sidebar.collapsed .nav-item {
  margin: 8px 12px;
}

.sidebar.collapsed .nav-link {
  justify-content: center;
  padding: 12px;
  gap: 0;
  background: transparent;
  transform: none;
}

.sidebar.collapsed .nav-link:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: none;
}

.sidebar.collapsed .nav-link.active {
  background: rgba(255, 255, 255, 0.12);
  box-shadow: none;
}

.sidebar.collapsed .nav-link.active::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 4px;
  height: 100%;
  background: #00d4ff;
}

.sidebar.collapsed .nav-icon {
  width: 36px;
  font-size: 20px;
}

.sidebar-header {
  padding: 24px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #00d4ff 0%, #5b73ff 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  flex-shrink: 0;
}

.brand-text {
  font-size: 18px;
  font-weight: 700;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.sidebar.collapsed .brand-text {
  opacity: 0;
  width: 0;
  overflow: hidden;
}

.toggle-btn {
  position: absolute;
  right: -12px;
  top: 32px;
  width: 24px;
  height: 24px;
  background: #00d4ff;
  border: none;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.toggle-btn:hover {
  background: #5b73ff;
  transform: scale(1.1);
}

.nav-menu {
  padding: 20px 0;
}

.nav-item {
  margin: 8px 16px; /*  */
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 20px;
  color: rgba(255, 255, 255, 0.7); /*  */
  text-decoration: none;
  border-radius: 12px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  /*  */
}

.nav-link.active {
  background: #374151; /*  */
  color: white;
  /*  */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.nav-icon {
  font-size: 20px;
  flex-shrink: 0;
  width: 24px;
  text-align: center;
  /*  */
  transition: transform 0.3s ease;
}

.nav-link:hover .nav-icon {
  transform: scale(1.1);
}

.nav-text {
  font-size: 15px; /*  */
  font-weight: 600; /*  */
  opacity: 1;
  transition: opacity 0.3s ease;
}

.sidebar.collapsed .nav-text {
  opacity: 0;
  width: 0;
  overflow: hidden;
}

.nav-description {
  font-size: 11px; /*  */
  font-weight: 400; /*  */
  color: rgba(255, 255, 255, 0.5); /*  */
  margin-top: 2px;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.sidebar.collapsed .nav-description {
  opacity: 0;
  width: 0;
  overflow: hidden;
}

/*  */
@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
    width: 280px;
  }
  
  .sidebar.mobile-open {
    transform: translateX(0);
  }
  
  .sidebar.collapsed {
    width: 280px;
  }
  
  .sidebar.collapsed .brand-text,
  .sidebar.collapsed .nav-text,
  .sidebar.collapsed .nav-description {
    opacity: 1;
    width: auto;
  }
}